<template>
    <div>
        <h1 style="margin: 20px;font-size: 36px;"></h1>
        <el-table :data="componentList" style="width: 1200px;text-align:left;margin: auto;">
            <el-table-column type="index" width="100">
            </el-table-column>
            <el-table-column prop="name" label="" width="200">
            </el-table-column>
            <el-table-column prop="introduce" label="" width="900">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                componentList: [{
                    name: 'element',
                    introduce: 'Element Vue 2.0 '
                }, {
                    name: 'sass',
                    introduce: 'CSS'
                }, {
                    name: 'bootstrap',
                    introduce: 'web'
                }, {
                    name: 'vue-cropper',
                    introduce: ''
                }, {
                    name: 'less',
                    introduce: 'Less  CSS  CSS Mixin CSS '
                }, {
                    name: 'xlsx',
                    introduce: 'jsExcel'
                }, {
                    name: 'Mint-UI',
                    introduce: ' Vue.js '
                }, {
                    name: 'cube-ui',
                    introduce: 'cube-ui  Vue.js '
                }, {
                    name: 'vue-quill-editor',
                    introduce: ' Quill Vue '
                }],
            }
        }
    }
</script>

<style scoped>
    h3 {
        background-color: #ddd;
        padding: 10px;
    }
</style>



